body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
}

.Navbar
{
  min-height: 40vh;
  background-color: #212121;
  background-image: url(/static/media/kefairport.faebcd2e.jpg);
  background-position-y: 50%;
  display: flex;
  flex-direction: row;
  align-items: center;
  color: #eee;
  justify-content: center;
}

.Navbar .Brand {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  padding: 1em;
  margin: 0;
  background-color: rgba(0,0,0,0.8);
  cursor: pointer;
}

.Navbar .Brand:hover {
  background-color: rgba(0,0,0,0.9);
}

.Navbar .Brand svg {
  padding: 0.25em;
}

.List {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-grow: grow;
}

.List .ListItem {
  padding: 1em;
  background: 
    -webkit-linear-gradient(
          #212121, 
          #121212
  );
  background: 
    linear-gradient(
          #212121, 
          #121212
  );
  color: #eee;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  border-right: 3px solid #212121;
}

.List .ListItem:last-of-type{
  border: 0;
}

.List .ListItem:hover {
  background: 
    -webkit-linear-gradient(
          #282828, 
          #181818
  );
  background: 
    linear-gradient(
          #282828, 
          #181818
  );
}

.List .ListItem svg {
  padding: 0.25em;
}
